#Projects {
  background-color: #385677;
  padding-top: 100px;
}

.title {
  text-align: center;
  font-size: 280%;
}

.ProjectCards {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.ProjectStyle {
  background-color: #385677;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: auto;
}

.ProjectCards #pcard {
  margin: 10px 10px 10px 10px;
  border: 7px solid rgb(36, 42, 56);
  width: 360px;
}

/* Carousel Styles */

.OtherCards {
  margin-top: 60px;
}

.slick-active {
  margin-left: auto;
  margin-right: auto;
}

.resourceCarouselSlides {
  max-width: 350px;
}

.resourceCarousel {
  height: 300px;
}

.slick-list {
  height: 300px;
  text-align: center;
}

.slick-arrow {
  height: 70px;
  width: 70px;
}

.slick-prev {
  position: relative;
  top: 20px;
}

.slick-prev:before,
.slick-next:before {
  font-size: 40px !important;
}

.slick-next:before {
  position: relative;
  top: 20px;
}



